body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* TODO: FOR BUGFIXING */
/* * { border: 2px solid red} */

.homepage {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow: hidden;

  min-height: 100vh;
  background-color: black;
}


/* ---- navigator ---- */
.navigatorWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.title {
  color: white;
  -webkit-align-self: center;
          align-self: center;

  font-size: 3rem;
}

.navigatorBar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.navigatorButton {
  color: white;
  cursor: pointer;
}

/* ---- body ---- */

.bodyWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.bodyElementTitle {
  color: white;
}

.inputWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

/* --- login --- */

.loginTitle {
  color: white;
  font-size: 3rem;
}

.pleaseLogin {
  color: white;
  font-size: 2rem;
}

.inputButton {
  color: white;
  cursor: pointer;
}
